.header {
    margin: 0;
    margin-top: 27px;
    max-width: 100%;
    margin-left: 5.85%;
    margin-right: 5.85%;
    gap: 33px;
    display: flex;
    flex-direction: row;
    align-items: flex-end;
    justify-content: flex-end;

}


.header__link {
    font-family: 'Heebo';
    font-style: normal;
    font-weight: 900;
    font-size: 20px;
    line-height: 29px;
    text-align: right;
    color: #000000;
    text-decoration: none;
    cursor: pointer;
}

@media screen and (max-width: 490px) {
    .header__link {
        font-size: 15px;
        line-height: 15px;
    }
}

@media screen and (max-width: 350px) {
    .header__link {
        font-size: 12px;
        line-height: 12px;
    }
}

.header__link:hover {
    scale: 1.1;
}

.header__link_active {
    color: rgba(51, 151, 89, 0.493);
}